export const ON_CHANGE = 'ON_CHANGE';
export const ON_CHANGE_CHECK = 'ON_CHANGE_CHECK';
export const ON_CHANGE_FORM = 'ON_CHANGE_FORM';
export const ADD_FORM = 'ADD_FORM';
export const REMOVE_FORM = 'REMOVE_FORM';
export const ON_CHANGE_DAYS = 'ON_CHANGE_DAYS';
export const RESET = 'RESET';
export const EDIT = 'EDIT';

export function onChange(name, value) {
    return { type: ON_CHANGE, name , value };
}

export function onChangeForm(e) {
    return { type: ON_CHANGE_FORM, name: e.currentTarget.name, value:e.currentTarget.value, id: e.currentTarget.id}
}

export function addForm() {
    return { type: ADD_FORM}
}

export function removeForm() {
    return { type: REMOVE_FORM}
}

export function onChangeDays(e)  {
    return { type: ON_CHANGE_DAYS, name: e.currentTarget.name};
}

export function reset() {
    return { type: RESET}
}

export function edit(sub) {
    return { type: EDIT, sub}
}