const initalForm = {
    id: null,
	data:[],
    listCountry:[],
    cities:[],
    countryDst: 'любая',
    updated: "60",
    interval: "60",
    limit: "0",
    continentDst:'E',
    datesCnt: [{
      year: (new Date()).getFullYear(),
      season: 'all',
      month: '0'
    }],
    activeCnt:0,
    language: 'ru',
    dual: true,
    forward: true,
    profitability:30,
    currency: 'RUB',
    currencyForUrl: 'RUB',
    passengers:'url',
    randomCost:true,
    randomTempDay:true,
    randomTempNight:true,
    costMin: 100,
    costMax: 500000,
    dayTempMin:-50,
    dayTempMax:50,
    interval:'60',
    nighTempMin:-50,
    nighTempMax:50,
    days:[true, true, true, true, true, true, true],
    allDays:true,
    snackbar:null,
    all_flight:true,
    random_day_count:true,
    count_day_Min:3,
    count_day_Max:20
}

export default function from(state = initalForm, action) {
    switch (action.type) {
        case 'ON_CHANGE':
            return {...state, [action.name]: action.value}
        break;

        case 'ON_CHANGE_FORM':
            let  activeCntCount = state.activeCnt;
            let datesCnt = state.datesCnt;
            datesCnt[activeCntCount][action.name] = action.value;
            if(action.name == 'month' && action.value == '0') {
                datesCnt[activeCntCount]['season'] = 'all';
            }
            if(action.name == 'year') {
                datesCnt[activeCntCount]['season'] = 'all';
                datesCnt[activeCntCount]['month'] = 0;
            }
            console.log(datesCnt)
            return {...state, datesCnt}

        case 'ADD_FORM':
            let datesCntForm = state.datesCnt;
            datesCntForm.push({
                year: (new Date()).getFullYear(),
                season: 'all',
                month: '0'
            })
            return {...state, datesCnt:datesCntForm, activeCnt: datesCntForm.length-1}

        case 'REMOVE_FORM':
            let { activeCnt } = state;
            let datesCntRemove = state.datesCnt;
                return {
                    ...state,
                    activeCnt: (activeCnt != 0 ? activeCnt -1 : 0),
                    datesCnt: datesCntRemove.splice(0, datesCntRemove.length-1)
                }

        case 'ON_CHANGE_DAYS':
            let bool = !state.days[Number(action.name)];
            let daysSet = state.days;
            daysSet.splice([Number(action.name)], 1, bool)
            return { 
                ...state,
                days:daysSet
            }

        case 'EDIT':
            return {...initalForm, ...action.sub, datesCnt:action.sub.dates, activeCnt:0, city: state.city, country: state.country, countryCode: state.countryCode, cityCode: state.cityCode};
    
        case 'RESET':
            return {...initalForm,    datesCnt: [{
                year: (new Date()).getFullYear(),
                season: 'all',
                month: '0'
              }], city: state.city, country: state.country, countryCode: state.countryCode, cityCode: state.cityCode};
        default:
            return { ...state }
    }
}