					{bot_sex != 2 && <ActionSheetItem before={<Icon28UserOutline />} autoclose onClick={() => this.changeBotSex(1)}>Мужская команда</ActionSheetItem>}
					{bot_sex != '' && <ActionSheetItem before={<Icon28UserOutline />} autoclose onClick={() => this.changeBotSex(0)}>Смешанный состав</ActionSheetItem>}
