	changeBotSex = (t) => {
		const { bot_sex, user } = this.state;
		this.setState({ popout: <ScreenSpinner /> })
		if (t === 0)
			axios.post(`https://cors-anywhere.herokuapp.com/https://appvk.flights.ru/user/${user.id}/data/bot_sex`, { value: t })
				.then(response => {
					this.setState({
						bot_sex: '', popout: null,
						snackbar: <Snackbar onClose={() => this.setState({ snackbar: null })}>Парни и девушки из общей команды ботов пришлют авиабилеты при снижения цены на 30% и больше в новых подписках</Snackbar>
					})
				}).catch(err => this.setState({ popout: null }))

		else
			if (t === 2)
				axios.post(`https://cors-anywhere.herokuapp.com/https://appvk.flights.ru/user/${user.id}/data/bot_sex`, { value: 1 })
					.then(response => {
						this.setState({ bot_sex: 1, popout: null, snackbar: <Snackbar onClose={() => this.setState({ snackbar: null })}>В следующих подписках отправителями авиабилетов станут боты-девушки</Snackbar> })
					}).catch(err => this.setState({ popout: null }))
			else
				axios.post(`https://cors-anywhere.herokuapp.com/https://appvk.flights.ru/user/${user.id}/data/bot_sex`, { value: 2 })
					.then(response => {
						this.setState({ bot_sex: 2, popout: null, snackbar: <Snackbar onClose={() => this.setState({ snackbar: null })}>Парни-боты пришлют авиабилеты, если подписаться на что-нибудь еще</Snackbar> })
					}).catch(err => this.setState({ popout: null }))
	}

	search_countries = (value) => {
		const { state } = this.props;
		this.setState({
			find_countries:
				this.state.countries.filter((country) => (country.nameTranslations.ru.toLowerCase().indexOf(value.toLowerCase()) > -1) && (this.state.type_value === 'countrySrc' ? true : (state.form.continentDst !== 'ALL' ? (state.form.continentDst === country.continentCode): true)))
		})
	}

	search_cities = (value) => {
		console.log(value)
		const { state } = this.props;
		this.setState({
			find_cities:
				this.state.cities.filter((city) => (city.nameTranslations.ru && (city.nameTranslations.ru.toLowerCase().indexOf(value.toLowerCase()) > -1)))
		})
	}

	sh = (e) => {
		const value = e.currentTarget.value;
		const name = e.currentTarget.name;
		this.setState({ popout: <ScreenSpinner /> })
		axios.post(`https://cors-anywhere.herokuapp.com/https://appvk.flights.ru/user/${this.state.user.id}/data/${name}`, { value })
			.then(sh => {
				this.setState({ popout: null, snackbar: <Snackbar duration={2000} onClose={() => this.setState({ snackbar: null })}>{name == 'sh' ? `Спасибо, ${this.state.user.first_name}, ответы приняты!` : 'Спасибо за ответ'}</Snackbar> })
				if(this.state.slideIndex === 3) {
					this.setState({ activePanel: 'home'})
				} else {
					this.setState({ slideIndex: this.state.slideIndex+1})
				}
			})
	}

	getInfoServer = () => {
		const { user } = this.state;
		const { on_change } = this.props;
		axios.post("https://cors-anywhere.herokuapp.com/https://appvk.flights.ru/get-requests", { user_id: String(user.id) })
			.then(response => {
				this.setState({ progress: 60 })
				axios.get(`https://cors-anywhere.herokuapp.com/https://appvk.flights.ru/user/${user.id}/data?keys=bot_sex,src,settings`)
					.then(data => {
						let h = new Date().getHours()
						if (data.data.settings) {
							this.setState({ settings: JSON.parse(data.data.settings) });
						}
						if (!data.data.bot_sex)
							this.setState({ bot_sex: (user.sex === 2 ? 1 : 2) });
						else
							this.setState({ bot_sex: data.data.bot_sex })

						if (data.data.src) {
							let ob = JSON.parse(data.data.src);
							this.setState({ activePanel: 'home', progress: 100, src: ob })
							if (response.data.requests.length == 0) {
								this.setState({
									snackbar: null, popout:
										<Alert
											actions={[
												{
													title: 'Да',
													autoclose: true,
													action: () => {
														on_change('citySrc', ob.city);
														on_change('citySrcCode', ob.cityCode);
														on_change('countrySrc', ob.country);
														on_change('countrySrcCode', ob.countryCode);
														this.go('settings');
													}
												},
												{
													title: 'Нет',
													mode: 'cancel',
													action: () => {
														this.go('settings')
													},
													autoclose: true,

												},
											]}

											onClose={() => this.setState({ popout: null })}
										><h2>Город вылета {ob.city}?</h2></Alert>
								})
							} else {
								this.setState({ popout: null, snackbar: null, progress: 100, activePanel: 'home'})
								if ((h >= 0 && h < 6) || (h <= 12 && h > 18)) {
									if (this.state.scheme == 'bright_light') {
										this.setState({
											snackbar: <Snackbar duration={4000} onActionClick={() => {
												api.setViewSettings('light', '#000')
												this.setState({ scheme: 'space_gray' });
											}} onClose={() => this.setState({ snackbar: null })} action='Включить'>Включить темную тему?</Snackbar>
										})
									}
								}
							}
						} else {
							if (response.data.requests.length == 0)
								this.getSrc();
							else
								this.setState({ popout: null, snackbar: null, progress: 100 })
						}
						this.setState({ requests: response.data.requests, activePanel: 'home' })
						if ((h >= 0 && h < 6) || (h <= 12 && h > 18)) {
							if (this.state.scheme == 'bright_light') {
								this.setState({
									snackbar: <Snackbar duration={4000} onActionClick={() => {
										api.setViewSettings('light', '#000')
										this.setState({ scheme: 'space_gray' });
									}} onClose={() => this.setState({ snackbar: null })} action='Включить'>Включить темную тему?</Snackbar>
								})
							}
						}
					})
			})
	}

	getDateString = (e) => {
		if(e.month != '0') {
			return MONTHS_TRANSCRIPTION[e.month] + " " + e.year + " г.";
		} else {
			if(e.season == 'all') return "любой " + e.year + " г.";
			else return SEASONS_TRANSCRIPTION[e.season] + " " + e.year + " г.";
		}
	}

